import axios from 'axios'
import Scroll from "../../components/scroll/scroll"
  export default {
      name: "test-components",
    components:{Scroll},
    data(){
      return{
        id:"",
        pullup: true,
        dataList:[]
      }
    },
    created(){
      this.id=this.$route.params.id;
      this.getData(this.id)
    },
    methods:{
      getData(id){
        axios.get("http://192.168.0.55:8899/customerLoginController.do?method=getMerchantsByAcId&activityId="+id+"&page=1&rows=10&loginType=2").then((res)=>{
          console.log(res.data)
          this.dataList=res.data.dataList
        })
      },
      //
      back() {
        this.$router.go(-1);
        this.$destroy();
      },
    },
  }



